import Button from '@mui/material/Button';
import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import { changeDisplay, drumStateSelection } from '../drum/drumSlice';
import { PadData } from './pads.data';
import React from 'react';
// import { useEffect } from 'react';

// const dispatch = useAppDispatch();
const body = document.querySelector('body')!;
console.log(`Log ~ file: Pad.tsx:10 ~ body`, body);

const playAudio = (keyLetter: string) => (document.getElementById(keyLetter)! as HTMLMediaElement).play();

body.addEventListener('keyup', (event) => {
  const pressedKey = event.key.toUpperCase();
  console.log({ key: event.key, pressedKey });
  if (!'QWEASDZXC'.includes(pressedKey)) return;

  // dispatch(
  //   changeDisplay({
  //     display: drumState.isHeaterBankKit ? padData.heaterBankKitDisplay : padData.smoothBankKitDisplay,
  //   })
  // );
  playAudio(pressedKey);
});

interface PadProps {
  padData: PadData;
}
const Pad: React.FunctionComponent<PadProps> = (props) => {
  const [padData] = React.useState(props.padData);
  const drumState = useAppSelector(drumStateSelection);
  const dispatch = useAppDispatch();

  console.log(padData.key);

  // React.useMemo(() => {
  // }, [padData.key]);

  // const body = document.querySelector('body')!;
  // body.onkeyup = (event) => {
  //   // const letter = event.key.toLowerCase();
  //   // if (!'qweasdzxc'.includes(letter)) return;
  //   playAudio();
  // };
  // const AudioContext = window.AudioContext; // || window.webkitAudioContext;
  // const audioContext = new AudioContext();
  // let audioElement: any;
  // useEffect(() => {
  //   audioElement = document.getElementById(letter)! as HTMLMediaElement;
  //   const track = audioContext.createMediaElementSource(audioElement);
  //   track.connect(audioContext.destination);
  // });
  return (
    <Button
      className='drum-pad'
      size='large'
      id={padData.key + '_pad'}
      onClick={() => {
        dispatch(
          changeDisplay({
            display: drumState.isHeaterBankKit ? padData.heaterBankKitDisplay : padData.smoothBankKitDisplay,
          })
        );
        playAudio(padData.key);
      }}
    >
      {padData.key}
      <audio
        id={padData.key}
        className='clip'
        src={
          process.env.PUBLIC_URL +
          '/media/' +
          (drumState.isHeaterBankKit ? padData.heaterBankKitFile : padData.smoothBankKitFile)
        }
      />
    </Button>
  );
};

export default Pad;
