import VolumeDown from '@mui/icons-material/VolumeDown';
import VolumeUp from '@mui/icons-material/VolumeUp';
import Grid from '@mui/material/Grid';
import Slider from '@mui/material/Slider';
import Stack from '@mui/material/Stack';
import Typography from '@mui/material/Typography';
import Box from '@mui/system/Box/Box';
import { useAppDispatch, useAppSelector } from '../../redux/hooks';
import Pad from '../pad/Pad';
import padsData from '../pad/pads.data';
import { drumStateSelection } from './drumSlice';
import Switch from '@mui/material/Switch';
import FormControlLabel from '@mui/material/FormControlLabel';

const Drum = () => {
  const drumState = useAppSelector(drumStateSelection);
  const dispatch = useAppDispatch();

  return (
    <Grid container id='drum-machine'>
      {/* Puds  */}
      <Grid item xs={10}>
        <Grid
          container
          spacing={1}
          direction='row'
          justifyContent='center'
          alignItems='center'
          alignContent='center'
          wrap='wrap'
        >
          {padsData.map((padData, index) => {
            return (
              <Grid item xs={4} justifyContent='center' alignItems='center' alignContent='center' key={padData.key}>
                <Pad padData={padData}></Pad>
              </Grid>
            );
          })}
        </Grid>
      </Grid>

      <Grid item xs={2}>
        <Grid container spacing={2} direction='column'>
          <Grid item xs={12}>
            <FormControlLabel control={<Switch defaultChecked />} label='Power' />
            {/* <Stack direction='row' spacing={1} alignItems='center'>
              <Typography>Off</Typography>
              <AntSwitch defaultChecked inputProps={{ 'aria-label': 'ant design' }} />
              <Typography>On</Typography>
            </Stack> */}
          </Grid>

          <Grid item id='display' xs={12} bgcolor={'GrayText'}>
            <Typography variant='h4' color='primary'>
              Display
            </Typography>

            <Typography variant='h6' color='primary'>
              {drumState.display}
            </Typography>
          </Grid>

          <Grid item xs={12}>
            <Stack spacing={2} direction='row' sx={{ mb: 1 }} alignItems='center'>
              <VolumeDown />
              <Slider aria-label='Volume' min={1} max={100} value={30} /* value={value} onChange={handleChange}  */ />
              <VolumeUp />
            </Stack>
          </Grid>
        </Grid>
      </Grid>
    </Grid>
  );
};

export default Drum;
