import { PayloadAction, createSlice } from '@reduxjs/toolkit';
import { RootState } from '../../redux/store';

// export const SMOOTH_PIANO_KIT = 'smooth';
// export const HEATER_KIT = 'heater';

interface DrumInitialState {
  isPowerOn: boolean;
  isHeaterBankKit: boolean;
  volume: number;
  display: string;
}

const initialState: DrumInitialState = {
  isPowerOn: true,
  isHeaterBankKit: true,
  volume: 30,
  display: '',
};

const drumReducer = createSlice({
  name: 'drum',
  initialState,
  reducers: {
    changeBankKit(state, action) {
      state.isHeaterBankKit = !state.isHeaterBankKit;
    },
    changeDisplay(state, action: PayloadAction<{ display: string }>) {
      console.dir(action, { depth: 4, showHidden: false, colors: true });
      state.display = action.payload.display;
    },
  },
});

export const { changeBankKit, changeDisplay } = drumReducer.actions;

export const drumStateSelection = (state: RootState) => state.drum;

export default drumReducer;
